import { Handler } from "aws-lambda";

export const simpleauth: Handler = async (event) => {
    // console.log('dsdsd')/
    return {
        principalId: "bilalnazir",
        policyDocument: {
            Version: "2012-10-17",
            Statement: [
                {
                    Resource: ["*"],
                    Effect: "Allow",
                    Action: "execute-api:Invoke"
                }
            ]
        }
    }
}